import ArtItem from './ArtItem';
import { Container, Row } from 'react-bootstrap';
import './Gallery.css';

function Gallery() {
  return (
    <div className='Gallery'>
      <Container>
        <Row xs={2} md={3} lg={4}>
          <ArtItem link='./pics/20230629 move that body.jpg'
            title='Pump It Up and Move That Body to Infinity' />
          <ArtItem link='./pics/20230617 pastorale.jpg'
            title='Pastorale' />
          <ArtItem link='./pics/20230108 jack stardust fancy (glasses ver).jpg'
            title='StardustFox (birthday sketch)' />
          <ArtItem link='./pics/20220822 self-forgiveness (upload ver).png'
            title='Self-forgiveness' />
          <ArtItem link='./pics/20220820 learning to appreciate myself (night ver) (upload ver).png'
            title='Learning to Appreciate Myself' />
          <ArtItem link='./pics/20220817 Jesse head portrait (upload ver).png'
            title='Head portrait' />
          <ArtItem link='./pics/20220718 Light (upload ver).jpg'
            title='Light' />
        </Row>

      </Container>
    </div>
  )
}

export default Gallery;