import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Row, Col, Container } from 'react-bootstrap';
// import { Button } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>content ratings</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range. 
        </p>
        <p id='introParagraph'>
          The content's overall context, tone, and psychological impact are important considerations
          for content rating classification. Hence, the following quick guidelines may not always
          be applied in the most literal manner. Flexibility in approach is advised.
        </p><br />

        {/* <Button href="https://docs.google.com/document/d/1AAXOFWNvRYhlmqHcRZL0Hgy2mW5zjyg-qeX8bB8v9mg/edit?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton' >
          FULL CONTENT RATING GUIDELINES
        </Button> */}
        <br />

        <Container className='ratingSummary'>
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/00+.png')} alt='0+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>Suitable for viewing by all ages.</h5>
              <ul>
                <li>Family-friendly themes addressed sensitively to preschoolers' sense of wellbeing and security. Mild crude humour.</li>
                <li>No profanity.</li>
                <li>Brief nudity with clear contextual justification. Kissing, embracing, etc. only.</li>
                <li>Occasional short animated, comedic, fantastic violence and scary scenes with contextual justification.</li>
                <li>Alcohol, tobacco, cannabis, or other drug use only in clearly educational contexts.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/06+.png')} alt='6+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>May not be suitable for viewing by children under 6.</h5>
              <ul>
                <li>Still just family-friendly themes. May have more detail on distress with contextual justification.</li>
                <li>A few non-aggressive uses of the mildest profanity (e.g., crap, damn, hell).</li>
                <li>Brief nudity with clear contextual justification. Kissing, embracing, etc. only.</li>
                <li>Mild violence. Mild, unsustained scary scenes. Overall reassuring resolution.</li>
                <li>Adult alcohol, tobacco, (very implicit) cannabis consumption without detail.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/09+.png')} alt='9+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>Not recommended for viewing by children under 9.</h5>
              <ul>
                <li>More serious real-life issues (e.g., cyberbullying, war, family issues), still treated with overall positive outcome.</li>
                <li>Mild coarse language. Some implied uses of strong language with contextual justification.</li>
                <li>Brief nudity with clear contextual justification. Some mild suggestive content and brief innuendoes.</li>
                <li>Greater violence (especially if fantastic or comedic). Frightening content not sustained or graphic.</li>
                <li>Alcohol, tobacco, cananbis not glamorized. A few mild references to drug use with contextual justification.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/12+.png')} alt='12+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>Not recommended for viewing by children under 12.</h5>
              <ul>
                <li>Mature themes (e.g., crime, politics, sexual harassment, drug abuse, suicide).</li>
                <li>Coarse language that may include some strong language used nonsexually or a few contextually justified slurs.</li>
                <li>Nonsexual nudity. Greater suggestive content and relatively mild sexual references.</li>
                <li>Moderate violence and more prolonged horror with some blood or injury detail.</li>
                <li>Occasional light imagery of drug use, substance abuse, or underage drinking/smoking, contextually justified.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/14+.png')} alt='14+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>Not recommended for viewing by minors under 14.</h5>
              <ul>
                <li>Any theme allowed. 14+ vs. 16+ vs. 18+ determined based on how themes are treated overall.</li>
                <li>Strong language that may include sexual expletives and discriminatory slurs.</li>
                <li>Sexual content (may include some undetailed nudity) not detailed, prolonged, frequent, or gratuitous.</li>
                <li>Intense violence that may be quite realistic and bloody. Strong horror. Implied sexual violence.</li>
                <li>More content related to drug use, substance abuse, or underage drinking/smoking in appropriate contexts.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/16+.png')} alt='16+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>Not recommended for viewing by minors under 16.</h5>
              <ul>
                <li>Any theme allowed. 14+ vs. 16+ vs. 18+ determined based on how themes are treated overall.</li>
                <li>Frequent strong language that may be aggressive.</li>
                <li>Greater sexual content that is not gratuitous or explicit.</li>
                <li>Some graphic violence and gore with contextual justification. Sexual violence not detailed.</li>
                <li>Drug use, substance abuse, or underage drinking/smoking without glamorization or instructional detail.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5>Suitable for viewing by adults 18 and up only.</h5>
              <ul>
                <li>Antisocial (harmful to society) or life-risking behaviour not condoned or encouraged overall.</li>
                <li>Discriminatory language not normalized or encouraged overall.</li>
                <li>Gratuitous or extremely crude nudity. Graphic sexual content. Any content overall intended to sexually arouse.</li>
                <li>Strong emphasis of explicit graphic violence and gore. Graphic sexual violence. Detailed suicide or self-harm.</li>
                <li>Contextually justified detail of hard drug use. No overall promotion of drugs or substance abuse.</li>
              </ul>
            </Col>
          </Row><br />


        </Container>
      </animated.div>
    </div>
  );
}

export default ContentRatings;
