import './App.css';
import React, { useEffect } from 'react';
import { Routes, Route, HashRouter } from 'react-router-dom';
import MyFooter from './components/MyFooter';
import Home from './components/pages/Home';
import Music from './components/pages/Music';
import Art from './components/pages/Art';
import DowntimeWithJesse from './components/pages/DowntimeWithJesse';
import About from './components/pages/About';
import ContentRatings from './components/pages/ContentRatings';
import Inspire from './components/pages/music/releases/Inspire';
// import TOS from './components/pages/TOS';


function App() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);


  return (
    <div className="App">
      <img className='backgroundImage' src={'/home.png'} alt='background'/>

      <HashRouter>
        <Routes>
          <Route exact path={'/'} element={<Home />} />
          <Route exact path={'/music'} element={<Music />} />
          <Route exact path={'/art'} element={<Art />} />
          <Route exact path={'/downtime_with_jesse'} element={<DowntimeWithJesse />} />
          <Route exact path={'/about'} element={<About />} />
          <Route exact path={'/content_ratings'} element={<ContentRatings />} />

          {/* Links to each music release */}
          <Route exact path={'/music/inspire'} element={<Inspire />} />
        </Routes>
      </HashRouter>
      <MyFooter />

    </div>
  );
}

export default App;
