import { Card, Row, Col } from 'react-bootstrap';
import './Album.css';


function Album(props) {
  return (
    <a href={props.link} className="Album">
      <Card className='albumCard'>
        <Card.Img variant="top" src={require("" + props.img)} className='coverArt' />
        <Card.Body>
          <Card.Title>{props.title}</Card.Title>
          <Card.Text>{props.subtitle}</Card.Text>
        </Card.Body>
        <Card.Footer style={{textShadow: 'none'}}>{props.date}</Card.Footer>
      </Card>

      <Card className='mobileAlbumCard'>
        <Row xs={2}>
          <Col>
            <img src={require("" + props.img)} alt='cover art' style={{ width: '100%', height: 'auto' }} />
          </Col>
          <Col>
            <Card.Body>
              <Card.Title>{props.title}</Card.Title>
              <Card.Text>{props.subtitle}</Card.Text>
            </Card.Body>
          </Col>
        </Row>
        <Card.Footer style={{textShadow: 'none'}}>{props.date}</Card.Footer>
      </Card>

    </a>
  );
}

export default Album;
