import './DowntimeWithJesse.css';
import MyNavbar from '../MyNavbar';
import { animated, useSpring } from '@react-spring/web';
import { Row, Col, Container, Button } from 'react-bootstrap';
import { useEffect } from 'react';


function DowntimeWithJesse() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  const discord_coming_soon = () => {
    alert("Jesse's Fox Den community Discord server is not yet open at the moment. Please be patient! >w<");
  }

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="DowntimeWithJesse">
      <MyNavbar active='downtime_with_jesse' />
      <animated.div style={fadein_props}>

        <h1 className='Title'>downtime with jesse</h1>

        <Container style={{ fontSize: '150%' }}>

          <Row className='Platforms'>
            <Col>
              <Button href="https://www.youtube.com/@downtimewjesse" target="_blank" rel="noopener noreferrer" id='ytBtn'>
                &nbsp;<i class="fa-brands fa-youtube" />&nbsp; Downtime W/ Jesse
              </Button>
              <Button href="https://www.twitch.tv/jessetonic" target="_blank" rel="noopener noreferrer" id='twBtn'>
                &nbsp;<i class="fa-brands fa-twitch" />&nbsp; jessetonic
              </Button>
            </Col>
          </Row>

          <br />

          <Row>
            <Col>
              I also like to play games and do other fun activities for entertainment during my downtime!
              Want to spend some downtime with me? Follow me on my second YouTube channel and my Twitch
              to watch me play games or do some other wacky thing!
            </Col>
          </Row>

          <br /><hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr><br />

          <Row xs={1} md={2}>
            <Col>
              What about directly talking or playing games with me? You may get a chance to be part of
              a Downtime W/ Jesse stream or video if you are in my Community Discord server!
            </Col>
            <Col className='Platforms'>
              <Button onClick={discord_coming_soon} id='discordBtn'>
                &nbsp;<i class="fa-brands fa-discord" />&nbsp; <strong><em>Jesse's Fox Den</em></strong> Discord server
              </Button>
            </Col>
          </Row>

          <br /><br />

          <Row>
            <Col style={{display: 'flex', justifyContent: 'center'}}>
              <img src={require('./downtime_with_jesse/boba pfp.png')} alt='profile' id='pfp' />
            </Col>
          </Row>

          <br /><br />

        </Container>

      </animated.div>
    </div>
  );
}

export default DowntimeWithJesse;
