import './Art.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
// import { Button } from 'react-bootstrap';
import { useEffect } from 'react';
import Gallery from './art/Gallery';


function Art() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="Art">
      <MyNavbar active='art' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>art</h1>

        <p>
          Click on each picture to see the full image.
        </p>
        <p>
          Full resolution images are available in my Discord server.
        </p>

        <Gallery/>

      </animated.div>
    </div>
  );
}

export default Art;
