import './MyNavbar.css';
import { React, useState } from 'react';
import { Navbar, Container, Nav, Offcanvas } from 'react-bootstrap';


function MyNavbar(props) {
  const [showMenu, setShowMenu] = useState(false);
  const handleClose = () => setShowMenu(false);
  const handleShow = () => setShowMenu(true);

  return (
    <div className="MyNavbar">
      <Navbar bg="dark" variant="dark" fixed='top' className='NavbarBar'>

        <Container>
          <Navbar.Brand href={'#/'} className='NavbarTitle'>
            jessetonic
          </Navbar.Brand>
        </Container>

        <div className="NavbarMenu">
          <Nav.Link href={'#/'} className={'NavbarLinks' + (props.active === '' ? ' active' : '')}>
            home
          </Nav.Link>
          <Nav.Link href={'#/music'} className={'NavbarLinks' + (props.active === 'music' ? ' active' : '')}>
            music
          </Nav.Link>
          <Nav.Link href={'#/art'} className={'NavbarLinks' + (props.active === 'art' ? ' active' : '')}>
            art
          </Nav.Link>
          <Nav.Link href={'#/downtime_with_jesse'} className={'NavbarLinks' + (props.active === 'downtime_with_jesse' ? ' active' : '')}>
            downtime w/ jesse
          </Nav.Link>
          <Nav.Link href={'#/content_ratings'} className={'NavbarLinks' + (props.active === 'content_ratings' ? ' active' : '')}>
            content ratings
          </Nav.Link>
          <Nav.Link href={'#/about'} className={'NavbarLinks' + (props.active === 'about' ? ' active' : '')}>
            about
          </Nav.Link>
          {/* <Nav.Link href={'#/tos'} className={'NavbarLinks' + (props.active === 'tos' ? ' active' : '')}>
            TOS
          </Nav.Link> */}
        </div>

        <i class="fa-solid fa-bars MenuIcon" onClick={handleShow}></i>
      </Navbar>

      <Offcanvas show={showMenu} onHide={handleClose} placement='top' className='MobileMenu'>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Menu</Offcanvas.Title>
        </Offcanvas.Header>
          <Offcanvas.Body className='MobileMenuBody'>
            <Nav.Link href={'#/'} className='MobileMenuLinks'>home</Nav.Link>
            <Nav.Link href={'#/music'} className='MobileMenuLinks'>music</Nav.Link>
            <Nav.Link href={'#/art'} className='MobileMenuLinks'>art</Nav.Link>
            <Nav.Link href={'#/downtime_with_jesse'} className='MobileMenuLinks'>downtime w/ jesse</Nav.Link>
            <Nav.Link href={'#/content_ratings'} className='MobileMenuLinks'>content ratings</Nav.Link>
            <Nav.Link href={'#/about'} className='MobileMenuLinks'>about</Nav.Link>
            {/* <Nav.Link href="/tos" className='MobileMenuLinks'>TOS</Nav.Link> */}
          </Offcanvas.Body>
      </Offcanvas>
    </div>
  );
}

export default MyNavbar;
