import './MyFooter.css';
import { Container } from 'react-bootstrap';
import { Navbar, Nav } from 'react-bootstrap';


function MyFooter() {
  const discord_coming_soon = () => {
    alert("Jesse's Fox Den community Discord server is not yet open at the moment. Please be patient! >w<");
  }

  return (
    <div >

      <Navbar bg="dark" variant="dark" fixed='bottom' className='MyFooter'>

        <Container>
          <Navbar.Brand className='Copyright'>
             Jesse Tonic 2023
          </Navbar.Brand>


          <Nav className='SocialMediaLinks'>
            <Nav.Link href="https://www.youtube.com/@jessetonic" target="_blank" rel="noopener noreferrer" className='SnsIcon'>
              <i class="fa-brands fa-youtube" />
            </Nav.Link>
            <Nav.Link href="https://www.instagram.com/jessetonic/" target="_blank" rel="noopener noreferrer" className='SnsIcon'>
              <i class="fa-brands fa-instagram" />
            </Nav.Link>
            <Nav.Link href="https://www.tumblr.com/jessetonic" target="_blank" rel="noopener noreferrer" className='SnsIcon'>
              <i class="fa-brands fa-tumblr" />
            </Nav.Link>
            <Nav.Link href="https://www.tiktok.com/@jessetonic" target="_blank" rel="noopener noreferrer" className='SnsIcon'>
              <i class="fa-brands fa-tiktok" />
            </Nav.Link>
            <Nav.Link href="https://www.twitch.tv/jessetonic" target="_blank" rel="noopener noreferrer" className='SnsIcon'>
              <i class="fa-brands fa-twitch" />
            </Nav.Link>
            <Nav.Link onClick={discord_coming_soon} className='SnsIcon'>
              <i class="fa-brands fa-discord" />
            </Nav.Link>
            
          </Nav>

        </Container>
      </Navbar>
    </div>
  );
}

export default MyFooter;
