import './Home.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import HomeOpening from './home/HomeOpening';
import HomeCards from './home/HomeCards';
import { Button } from 'react-bootstrap';
import { useEffect } from 'react';

function Home() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  const discord_coming_soon = () => {
    alert("Jesse's Fox Den community Discord server is not yet open at the moment. Please be patient! >w<");
  }

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <MyNavbar active='' />
      <div className="Home">
        <animated.div style={fadein_props}>
          <HomeOpening/>
          <br/><br/>
          <Button onClick={discord_coming_soon} id='DiscordButton'>
            &nbsp;<i class="fa-brands fa-discord" />&nbsp; Join <strong><em>Jesse's Fox Den</em></strong> Discord server!
          </Button>
          <HomeCards/>
          
        </animated.div>
      </div>
    </>
  );
}

export default Home;