import MyNavbar from '../../../MyNavbar';
import { Button, Carousel, Col, Container, Row } from 'react-bootstrap';
import { animated, useSpring } from '@react-spring/web';
import { useEffect, useState } from 'react';
import '../Releases.css';

function Inspire() {
  const [width, setWidth] = useState(window.innerWidth * (window.innerWidth < 768 ? 0.8 : 0.65));

  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  window.addEventListener('resize', function (event) {
    setWidth(window.innerWidth * (window.innerWidth < 576 ? 0.8 : 0.65));
  }, true);

  return (
    <div className="Release">
      <MyNavbar />
      <animated.div style={fadein_props}>

        <h1 className='releaseTitle'>Inspire</h1>
        <h4>Digital Single</h4>
        <h5>15 March 2023</h5>
        <br />

        <Container>

          <Row className='biography' xs={1} md={2}>
            <Col style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
              <img src={require('../images/inspire.jpg')} style={{ width: '75%' }} alt='profile' />
            </Col>

            <Col className='albumDesc'>

              <Button className='streamButton' href='https://distrokid.com/hyperfollow/jessetonic/inspire-2'
                target="_blank" rel="noopener noreferrer" variant='success'>
                STREAM THE MUSIC
              </Button>
              <br /><br />
              <p>
                "Inspire" is my debut single. It is an orchestral piece that captures the uplifting,
                majestic feelings of finding inspiration and motivation to begin a new adventure in life,
                just as I am making a new beginning in my career as a musician, composer, and artist with the
                release of this track.
              </p>

            </Col>
          </Row>

          <br /><hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr><br />

          <Container>
            <h2>TRACKLIST</h2>
            <br />
            <div className='tracklist'>
              <Row>
                <Col xs={1}>1</Col>
                <Col xs={8} md={5}>Inspire</Col>
                <Col xs={1}>3:35</Col>
              </Row>
            </div>
          </Container>

          <br /><hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr><br />

          <Container>
            <h2>ARTWORK</h2>
            <br />
            <div className='tracklist'>
              <Carousel>
                <Carousel.Item>
                  <img
                    className="d-flex w-75 carouselImage"
                    src={require('../images/inspire 001 (upload).jpg')}
                    alt="Music Video Art 1"
                  />
                </Carousel.Item>
                <Carousel.Item>
                  <img
                    className="d-flex w-75 carouselImage"
                    src={require('../images/inspire 002 (upload).jpg')}
                    alt="Music Video Art 2"
                  />
                </Carousel.Item>
                <Carousel.Item>
                  <img
                    className="d-flex w-75 carouselImage"
                    src={require('../images/inspire 003 (upload).jpg')}
                    alt="Music Video Art 3"
                  />
                </Carousel.Item>
              </Carousel>
            </div>
          </Container>

          <br /><hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr><br />

          <Container>
            <h2>OFFICIAL VIDEO</h2>
            <br />
            <iframe className='officialVideo'
              src="https://www.youtube.com/embed/OTlN-ZdousQ"
              title="YouTube video player" frameborder="0" width={width} height={width * 0.5625}
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              allowfullscreen></iframe>
          </Container>

        </Container>
      </animated.div>
    </div>
  );
}

export default Inspire;