import { Card } from 'react-bootstrap';
import './HomeCards.css';

function HomeCards() {
  return (
    <div className="HomeCards">

      <a href='#/about'>
        <Card id='aboutCard'>
          {/* <Card.Img variant="top" src="holder.js/100px160" /> */}
          <i class="fa-solid fa-person-circle-question placeholderIcons"></i>
          <Card.Body>
            <Card.Title>ABOUT</Card.Title>
            <Card.Text>
              Well, who the heck am I? Where can you find me on the Internet?
            </Card.Text>
          </Card.Body>
        </Card>
      </a>
      <a href='#/music'>
        <Card id='musicCard'>
          {/* <Card.Img variant="top" src="holder.js/100px160" /> */}
          <i class="fa-solid fa-music placeholderIcons"></i>
          <Card.Body>
            <Card.Title>MUSIC</Card.Title>
            <Card.Text>
              Check out my musical explorations, including my discography,
              covers, music videos, and more!
            </Card.Text>
          </Card.Body>
        </Card>
      </a>
      <a href='#/art'>
        <Card id='artCard'>
          {/* <Card.Img variant="top" src="holder.js/100px160" /> */}
          <i class="fa-solid fa-paintbrush placeholderIcons"></i>
          <Card.Body>
            <Card.Title>ART</Card.Title>
            <Card.Text>
              Check out the gallery of all my drawings and visual artworks!
            </Card.Text>
          </Card.Body>
        </Card>
      </a>
      <a href='#/downtime_with_jesse'>
        <Card id='downtimeCard'>
          {/* <Card.Img variant="top" src="holder.js/100px160" /> */}
          <i class="fa-solid fa-headset placeholderIcons"></i>
          <Card.Body>
            <Card.Title>DOWNTIME W/ JESSE</Card.Title>
            <Card.Text>
              Wind down with me, and watch me play games and do some other fun things.
            </Card.Text>
          </Card.Body>
        </Card>
      </a>



    </div>
  );
}

export default HomeCards;