import { useState } from 'react';
import { Figure, Modal } from 'react-bootstrap';
import './ArtItem.css';

function ArtItem(props) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <div className='ArtItem'>
      <Figure onClick={handleShow}>
        <Figure.Image alt='art' src={require('' + props.link)}/>
      </Figure>

      <Modal centered show={show} onHide={handleClose} size='xl' dialogClassName='artModal'>
        <Modal.Header closeButton style={{padding: '4px 12px'}}>
          <Modal.Title>{props.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{display: 'flex', justifyContent: 'center', padding: '4px'}}>
          <img alt='art' src={require('' + props.link)} className='modalPicture' />
        </Modal.Body>
      </Modal>
    </div>
  )
}

export default ArtItem;